*{
	margin: 0;
	padding: 0;
	border: hidden;
}

body{
	overflow-x: hidden;
}

.orange{
	background-color: hsl(15, 100%, 70%);
}

.blue{
	background-color: hsl(195, 74%, 62%);
}

.red{
	background-color: hsl(348, 100%, 68%);
}

.green{
	background-color: hsl(145, 58%, 55%);
}

.purple{
	background-color: hsl(264, 64%, 52%);
}

.yellow{
	background-color: hsl(43, 84%, 65%)
}

.cardSmall:hover{
	background-color: #34397b;
}

#img{
	height: 50px;
	padding-right: 20px;
}

.dots{
	display: inline-block;
	margin-left: 5em;
}

.dots:hover{
	transform: scale(1.1);
}

.hours{
	font-family: 'Rubik';
	font-weight: 300;
	font-size: 50px;
}

.name{
	font-family: 'Rubik';
	font-weight: 400;
	font-size: 20px;
}

.week-hours{
	font-family: 'Rubik';
	font-weight: 400;
	color: hsl(236, 100%, 87%);
	font-size: 12px;
}

#wrapper{
	display: flex;
	width: 90%;
	margin: auto;
	margin-bottom: 15px;
	margin-top: 15px;
}

.card-wrapper{
	margin: auto;
	display: flex;
	width: 100%;
}

#cardBig{
	border-radius: 20px 20px 20px 20px;
	margin: 0px;
	width: 100%;
}

.cardSmall{
	background-color: hsl(235, 46%, 20%);
	color: #ffff;
	border-radius: 20px;
	z-index: +1;
	position: relative;
	height: 145px;
}

.img-div{
	display: flex;
	justify-content: right;
	height: 50px;
}

.name-dot{
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-left: 25px;
	margin-right: 25px;
	padding-top: 20px;
}

.hours-div{
 	display: flex;
 	align-items: center;
 	justify-content: space-between;
 	margin-left: 25px;
 	margin-right: 25px;
	padding-top: 20px;
}


@media only screen and (min-width: 560px){
	#wrapper{
		width: 70%;
	}
}

@media only screen and (min-width: 700px){
	#wrapper{
		width: 60%;
	}
}

@media only screen and (min-width: 854px){
	#wrapper{
		width: 50%;
	}
}

@media only screen and (min-width: 1024px){
	#wrapper{
		width: 225px;
		height: 225px;
		margin: auto;
		margin-bottom: 25px;
		margin-top: 25px;
		display: flex;
	}
	
	.card-wrapper{
		margin: auto;
		display: block;
		width: 100%;
	}
	
	#cardBig{
		border-radius: 20px 20px 20px 20px;
		margin: 0px;
		width: 100%;
	}
	
	.cardSmall{
		background-color: hsl(235, 46%, 20%);
		color: #ffff;
		border-radius: 20px;
		z-index: +1;
		position: relative;
		height: 165px;
	}
	
	.img-div{
		display: flex;
		justify-content: right;
		height: 50px;
	}
	
	.name-dot{
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-left: 25px;
		margin-right: 25px;
		padding-top: 20px;
	}
	
	.hours-div{
		display: block;
		align-items: center;
		justify-content: space-between;
		margin-left: 25px;
		margin-right: 25px;
		padding-top: 20px;
	}
}

@media only screen and (min-width: 1440px){
	#wrapper{
		display: inline-flex;
		margin-right: 10px;
		margin-left: 10px
	}
}