body{
  background-color: hsl(226, 43%, 10%);
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}


@media only screen and (min-width: 1024px){
  .app{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
  }

  .container{
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
}

@media only screen and (min-width: 1440px){
  .app{
    display:flex; 
    align-items: center;
    justify-content: center;
  }

  .container{
    display: grid;
    grid-template-columns: 1fr 2fr;
    width: 90%;
  }
}

@media only screen and (min-width: 1496px){
  .container{
  width: 80%;
  }
}

@media only screen and (min-width: 1640px){
  .app{
    display:flex; 
    align-items: center;
    justify-content: center;
  }

  .container{
    display: grid;
    grid-template-columns: 1fr 2fr ;
    width: 50%;
    grid-gap: 0;
    margin: 0;
  }
}

@media only screen and (min-width:1638px){
  .container{
    width: 70%;
    }
}

@media only screen and (min-width:1924px){
  .container{
    width: 60%;
    }
}

@media only screen and (min-width:2232px){
  .container{
    width: 50%;
    }
}