body{
	font-family: 'Rubik';
	font-weight: 300;
	color: #ffff;
	overflow-x: hidden;
	margin: 0;
  	padding: 0;
}

.jeremy-img{
	width: 70px;
	padding-left: 10px;
}

.report{
	font-family: 'Rubik';
	font-weight: 300;
	font-size: 12px;
	padding-left: 30px;
}

.userName{
	font-family: 'Rubik';
	font-weight: 300;
	font-size: 30px;
	padding-left: 30px;
}

.h3{
	font-family: 'Rubik';
	font-weight: 300;
	font-size: 12px;
	padding-bottom: 20px;
	color: hsl(236, 100%, 87%);
	padding-left: 30px;
}

.h3:hover{
	color: #ffff;
}

.bigWrapper{
	margin: auto;
	width: 90%;
	margin-top: 50px;
}

.userWrapper{
	background-color: hsl(235, 46%, 20%);
	border-radius: 20px;
}

.jeremy{
	background-color: #5746ea;
	border-radius: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 175px;
}

.writting{
	display: flex;
	justify-content: space-between;
	margin: 10px 60px 0px 30px;
}

@media only screen and (min-width: 560px){
	.bigWrapper{
		width: 70%;
	}
}

@media only screen and (min-width: 700px){
	.bigWrapper{
		width: 60%;
	}
}

@media only screen and (min-width: 854px){
	.bigWrapper{
		width: 50%;
	}
}

@media only screen and (min-width: 1024px){
	.bigWrapper{
		margin: auto;
		width: 225px;
		margin-top: 50px;
		display: flex;
	}
	
	.jeremy{
		border-radius: 20px;
		display: block;
		height: 300px;
		padding-left: 30px;
	}
	
	.writting{
		display: block;
		justify-content: space-between;
		margin: 20px 0 0 0;
	}

	.jeremy-img{
		width: 70px;
		padding-left: 0px;
		padding-top: 40px;
	}
	
	.report{
		font-family: 'Rubik';
		font-weight: 300;
		font-size: 12px;
		padding-left: 0px;
		padding-top: 20px;
	}
	
	.userName{
		font-family: 'Rubik';
		font-weight: 300;
		font-size: 35px;
		padding-left: 0px;
	}

	.h3{
		font-size: 14px;
	}
}

@media only screen and (min-width: 1440px){
	.bigWrapper{
		display: flex;
	}
}